import { editor } from "./editor";

export default class Sort {

    static getContainer () {
        return this.$selection.items.length === 1 ? this.$selection.currentArtboard : this.$selection.allRect;
    }

    static left () {

        var x =  Sort.getContainer().screenX.value

        this.$selection.each(item => {
            item.setScreenX(x);
        })

        this.$selection.setRectCache();
    }

    static top () {
        var y =  Sort.getContainer().screenY.value

        this.$selection.each(item => {
            item.setScreenY(y);
        })

        this.$selection.setRectCache();
    }

    static center () {
        var container = Sort.getContainer()        
        var x = container.screenX.value + container.width.value / 2; 

        this.$selection.each(item => {
            item.setScreenXCenter(x);
        })

        this.$selection.setRectCache();
    }

    static middle () {
        var container = Sort.getContainer()        
        var y = container.screenY.value + container.height.value / 2;         

        this.$selection.each(item => {
            item.setScreenYMiddle(y);
        })

        this.$selection.setRectCache();
    }    

    static right () {
        var container = Sort.getContainer()        
        var x2 = container.screenX2.value;         

        this.$selection.each(item => {
            item.setScreenX2(x2);
        })

        this.$selection.setRectCache();
    } 


    static bottom () {
        var container = Sort.getContainer()        
        var y2 = container.screenY2.value;         

        this.$selection.each(item => {
            item.setScreenY2(y2);
        })

        this.$selection.setRectCache();
    }     

    static sameWidth () {

        var len = this.$selection.items.length ;

        if (len == 1) {
            // artboard    . 
        } else if (len > 1) {
            this.$selection.each(item => {
                item.setScreenX(this.$selection.allRect.x.value);
                item.width.set( this.$selection.allRect.width.value);
            })

            this.$selection.setRectCache();            
        }


    }

    static sameHeight () {

        var len = this.$selection.items.length ;

        if (len == 1) {
            // artboard    . 
        } else if (len > 1) {
            this.$selection.each(item => {
                item.setScreenY(this.$selection.allRect.y.value);
                item.height.set( this.$selection.allRect.height.value);
            })

            this.$selection.setRectCache();
        }
    }
}