import UIElement, { EVENT } from "../../../util/UIElement";
import { LOAD } from "../../../util/Event";
import { CSS_TO_STRING, STRING_TO_CSS } from "../../../util/functions/func";
import SelectIconEditor from "./SelectIconEditor";

export default class FlexLayoutEditor extends UIElement {

    components() {
        return {
            SelectIconEditor,
        }
    }

    initState() {
        return {
            ...STRING_TO_CSS(this.props.value),
        }
    }

    setValue (value) {
        this.setState({
            ...STRING_TO_CSS(this.props.value),
        })
    }

    getValue () {
        return CSS_TO_STRING(this.state)
    }

    modifyData() {
        this.parent.trigger(this.props.onchange, this.props.key, this.getValue())
    }

    [LOAD('$body')] () {
        return /*html*/`
            <div class='flex-layout-item'>
                <div class='label'><label>direction</label></div>
                <SelectIconEditor 
                    key='flex-direction'
                    value="${this.state['flex-direction']}"
                    options="row,column,row-reverse,column-reverse"
                    onchange='changeKeyValue'
                />
            </div>
            <div class='flex-layout-item'>
                <div class='label'><label>wrap</label></div>
                <SelectIconEditor 
                    key='flex-wrap'
                    value="${this.state['flex-wrap']}"
                    options="nowrap,wrap,wrap-reverse"
                    onchange='changeKeyValue'
                />
            </div>
            <div class='flex-layout-item'>
                <div class='label'><label>justify content</label></div>
                <SelectIconEditor 
                    key='justify-content'
                    value="${this.state['justify-content']}"
                    options="flex-start,flex-end,center,space-between,space-around"
                    onchange='changeKeyValue'
                />
            </div>
            <div class='flex-layout-item'>
                <div class='label'><label>align-items</label></div>
                <SelectIconEditor 
                    key='align-items'
                    value="${this.state['align-items']}"
                    options="flex-start,flex-end,center,baseline,stretch"
                    onchange='changeKeyValue'
                />
            </div>
            <div class='flex-layout-item'>
                <div class='label'><label>align-content</label></div>
                <SelectIconEditor 
                    key='align-content'
                    value="${this.state['align-content']}"
                    options="flex-start,flex-end,center,space-between,space-around,stretch"
                    onchange='changeKeyValue'
                />
            </div>    
        `
    }

    template () {
        return /*html*/`
            <div class='flex-layout-editor' ref='$body' ></div>
        `
    }


    [EVENT('changeKeyValue')] (key, value, params) {

        this.setState({
            [key]: value
        }, false)

        this.modifyData();
    }
}