import { Item } from "./Item";
import { CSS_TO_STRING } from "../../util/functions/func";


export class Property extends Item {

  getDefaultObject(obj = {}) {
    return {
      selected: false,  //    
      layers: [],   //   . 
      ...obj
    };
  }

  isAttribute() {
    return true;
  }

  toCSS() { 
    return {};
  }

  toString() {
    return CSS_TO_STRING(this.toCSS());
  }
}
